@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

* {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

.navbar {
  position: sticky;
  top: 0;
  background-color: #fafbfc;
  height: 70px;
  display: flex;
  align-items: center;
  z-index: 10;
  box-shadow: inset 2px 2px 2px 0px rgba(60, 60, 60, 0.5),
    5px 5px 16px 0px rgba(255, 255, 255, 0.1),
    4px 4px 5px 0px rgba(49, 48, 48, 0.1);

}

.navbar img {
  height: 55px;
  padding-left: 15px;
}

.navbar .links {
  margin: auto;
}

.navbar .links ul {
  display: flex;
}

.navbar .links ul li {
  list-style: none;
  padding: 5px;
}

.navbar .links ul li:hover {
  cursor: pointer;
}

.home {
  /* background-color: #101010; */
  height: calc(100vh - 70px);
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.home .container {
  margin: auto;
  width: 500px;
  /* border: 2px solid #1c1c1c; */
  min-width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home .container .tagline {
  font-size: 45px;
  padding: 35px 0;
}

.home .container p {}


.home .imgs {
  height: 50%;
  padding: 0 80px;
  margin: auto;
  /* border: 2px solid #1c1c1c; */
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
}

.home .imgs img {
  height: 250px;
  border-radius: 32px;
  box-shadow: 2px 2px 10px 1px rgb(35, 123, 255);
}

.events {
  /* background-color: #2563EB; */
  background: linear-gradient(#009FFD,
      #2A2A72);
  height: calc(100vh - 70px);
}

.events .upcomingEvents {
  /* border: 2px solid #1c1c1c; */
  height: 42%;
  margin: 0 2%;
  padding: 1%;
  color: white;
}

.events .upcomingEvents p {
  font-weight: bold;
  font-size: 25px;
}

.events .upcomingEvents .eventList {
  /* border: 2px solid #7b00ff; */
  display: flex;
  height: 80%;
  margin-top: 15px;
}

.events .upcomingEvents .eventList .eventItem {
  /* border: 2px solid #1c1c1c; */
  background-color: #fafbfcaf;
  color: black;
  border-radius: 12px;
  width: 170px;
  margin: 15px;
  position: relative;
}

.eventItem .eventTitle {
  padding-top: 5px;
  text-align: center;
  /* background-color: red; */
  height: 32px;
  font-size: 25px;

}

.eventItem .eventDescription p {
  font-size: 15px;
  font-weight: 400;
  padding: 5px;
}

.eventItem .btn {
  position: absolute;
  bottom: 12px;
  /* background-color: red; */
  width: 100%;
  display: flex;
  justify-content: center;
}

.eventItem .btn button {
  height: 25px;
  width: 100px;
  border: 0;
  border-radius: 12px;
  cursor: pointer;
}

.about {
  height: calc(100vh - 70px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.about .aboutInfo {
  /* border: 2px solid #1c1c1c; */
  height: 70%;
  width: 80%;
}

@media (max-width: 400px) {
  .navbar {
    height: 50px;
    font-size: 14px;
  }

  .navbar img {
    height: 40px;
  }

  .home {
    height: calc(100vh - 50px);
  }

  .home .container {
    width: 98vw;
    padding: 0 10px;
  }

  .home .container .tagline {
    font-size: 25px;
  }

  .home .container p {
    font-size: 14px;
  }

  .home .imgs {
    height: 35%;
    padding: 20px 20px;
    background-color: red;
    width: 100vw;
  }

  .home .imgs img {
    height: 90%;
  }

  .events {
    height: calc(100vh - 50px);
  }

  .events .upcomingEvents .eventList {
    margin-top: 5px;
  }

  .events .upcomingEvents .eventList .eventItem {
    width: 120px;
  }

  .eventItem .eventTitle {
    font-size: 18px;
  }

  .eventItem .eventDescription p {
    font-size: 12px;
  }

  .eventItem .btn button {
    height: 20px;
    width: 75px;
    font-size: 12px;
  }

  .about {
    height: calc(100vh - 50px);
    font-size: 14px;
  }
}

@media (min-width: 400px) and (max-width: 600px) {
  .home .container {
    width: 98vw;
    padding: 0 10px;
    margin: 1%;
  }

  .home .container .tagline {
    font-size: 28px;
    padding: 10px 30px;
  }

  .home .imgs {
    height: 220px;
    padding: 20px 20px;
    /* background-color: red; */
    width: 100vw;
  }

  .home .imgs img {
    height: 90%;
  }

  .events .upcomingEvents .eventList .eventItem {
    width: 130px;
  }

  .eventItem .eventTitle {
    font-size: 18px;
  }

  .eventItem .eventDescription p {
    font-size: 12px;
  }

  .eventItem .btn button {
    height: 20px;
    width: 75px;
    font-size: 12px;
  }
}